.contact-header{
    font-size: xx-large;
    font-weight: 900;
    font-family: Arial, Helvetica, sans-serif
}
.contact-textarea ,.contact-input{
    border: none;
    background: rgb(248, 249, 250);
    width: 100%
}
.contact-input{

}
.contact-submit{
    border: none;
}
.contact-error{
    font-size: x-small;
    color: red;
}
.mail-response{
    position: fixed;
    z-index: 8;
    top:0px;
    left: 0px;
    right: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    background-color: rgba(128, 128, 128, 0.192);
}
.mail-response-img{
    max-height: 70px;
}
.mail-response-header{
    font-family: Arial, Helvetica, sans-serif;
}
.mail-response-close{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 25px;
    width: 25px;
    border-radius: 50%;
    border: 1px solid rgb(128, 128, 128);
    position: absolute;
    right: 10px;
    top: 10px;
    cursor: pointer;
    transition: 1s all ease-in-out;
}
.mail-response-close:hover{
    border: none;
    transition: 1s all ease-in-out;
}
.mail-response-close:hover i{
    color: rgb(128, 128, 128) ;
    transition: 1s all ease-in-out;

}