.signup-form{
    font-size: large;
    animation:4s 0s 2 all; 
}
.signup-select{
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 1px solid grey;
    outline: none!important;
    position: relative;
}
.signup-select option{
    font-size: xx-small;
    outline: none!important;
}
.password-input-container{
    
}
.resend-token{
    cursor: pointer;
    padding: 4px 10px;
    border:  1px solid gray;
}
.resend-token:hover{
    border: 1px solid rgba(128, 128, 128, 0.411);
    color: rgba(248, 248, 255, 0.39);

}
.resend-token:focus{
    border: 1px solid rgba(128, 128, 128, 0.411);
    color: rgba(248, 248, 255, 0.39);
}
