.navbar-search input , .navbar-search button{
    border:1px solid white;
    background: transparent;
    color: white;
    outline: none!important
}
.navbar-logo{
    height: 40px;
}
.navbar-search input{
    border-right: none!important;
    color: white;
    border-radius: 10px 0px 0px 10px;
}
.navbar-search button{
    border-left: none!important;
    border-radius: 0px 10px 10px 0px

}
.navbar-fixed-onScroll{
    /* border-bottom: 1px solid rgba(128, 128, 128, 0.63); */
    background: white;
}
.nav-link{
    font-size: 14px!important;
}
.navbar-user-round{
    border: 1px solid white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    width: 30px;
    cursor: pointer;
    /* transition: all 1s ease-in-out;
    -o-transition: all 12 ease-in-out;
    -moz-transition: all 12 ease-in-out;
    -webkit-transition: all 12 ease-in-out; */
}

.navbar-user-round i {
    cursor: pointer;
}
.navbar-user-round:hover i{
    color:rgb(41, 41, 41);
    transition: all 1s ease-in-out;
    -o-transition: all 12 ease-in-out;
    -moz-transition: all 12 ease-in-out;
    -webkit-transition: all 12 ease-in-out;


}
.navbar-user-round:hover{
    border: 1px solid rgb(41, 41, 41);
    transition: all 1s ease-in-out;
    -o-transition: all 12 ease-in-out;
    -moz-transition: all 12 ease-in-out;
    -webkit-transition: all 12 ease-in-out;


}